import React, {
  ReactNode,
  FC
} from 'react';
import { css } from '@emotion/react';

export interface ButtonProps {
  children: ReactNode;
  onClick?: () => void;
}

const Button: FC<ButtonProps> = ({
  children,
  onClick
}: ButtonProps) => {

  return (
    <button
      onClick={onClick}
      css={css`
        color: red; 
      `}
    >
      {children}
    </button>
  )
}

export default Button;
